        transparent calc(3.375em - 0.5 * var(--line-thickness)), currentcolor calc(3.375em - 0.5 * var(--line-thickness)), currentcolor calc(3.375em + 0.5 * var(--line-thickness)), transparent calc(3.375em + 0.5 * var(--line-thickness)),
